import React, { useState } from 'react';
import './BarTest.css';

function Bar() {
  const [progress, setProgress] = useState(0);

  const handleRightClick = () => {
    setProgress(prevProgress => Math.min(prevProgress + 10, 100));
  };

  const handleX2RightClick = () => {
    setProgress(prevProgress => Math.min(prevProgress + 20, 100));
  };

  const handleX2LeftClick = () => {
    setProgress(prevProgress => Math.max(prevProgress - 20, 0));
  };

  const handleLeftClick = () => {
    setProgress(prevProgress => Math.max(prevProgress - 10, 0));
  };

  return (
    <div className="bar">
      <div className="status-bar">
        <div className="progress" style={{ width: `${progress}%` }}></div>
      </div>
      <button onClick={handleX2LeftClick}>x2left</button>
      <button onClick={handleLeftClick}>left</button>
      <button onClick={handleRightClick}>right</button>
      <button onClick={handleX2RightClick}>x2right</button>
    </div>
  );
}

export default Bar;